  border-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  display: none;
  position: fixed;
  bottom: 40px;
  left: calc(50% - 25px);
}

.back-to-top:hover, .back-to-top:focus {
  background-color: var(--hover-color);
  outline: none;
