.advantages {
    &__section {
        background-color: var(--secondary-bcg-color);
        margin: 0;
    }

    &__container {
        padding: 40px 70px 60px 20px;

        @media (min-width: 768px) {
            min-width: 768px;
            padding: 60px 32px;
        }

        @media (min-width: 1200px) {
            min-width: 1200px;
            padding: 60px 16px;
        }
    }

    &__list {
        padding: 0;
        margin: 0;

        @media (min-width: 768px) {
            display: flex;
        }
    }

    &__item {
        margin: 0;
        margin-bottom: 32px;
        max-width: 230px;
        display: flex;
        flex-direction: column;

        @media (min-width: 768px) {
            margin-bottom: 0;
            max-width: 180px;
            margin-right: 13px;
        }

        @media screen and(min-width: 1200px) {
            max-width: 298px;
        }

        &:last-child {
            @media screen and(min-width: 320px) {
                margin-bottom: 0;
            }

            @media screen and(min-width: 768px) {
                margin-right: 0;
            }
        }
    }

    &__text {
        color: var(--third-text-color);
        font-family: Titan One;
        font-weight: 400;
        font-size: 36px;
        line-height: 1.14;
        margin: 0;
        margin-top: 16px;
        margin-bottom: 4px;
        width: 98px;
        height: 56px;

        @media (min-width: 768px) {
        width: 147px;
        }

        @media (min-width: 768px) {
            font-size: 48px;
            line-height: 1.15;
            margin-top: 36px;
            margin-bottom: 13px;
        }

        @media (min-width: 1200px) {
            font-size: 58px;
            line-height: 1.14;
            margin-top: 40px;
            margin-bottom: 16px;
        }
    }

    &__description {

        padding: 0;
        margin: 0;

        font-weight: 700;
        font-size: 12px;
        line-height: 1.67;

        @media (min-width: 768px) {
            font-size: 14px;
            line-height: 1.71;
        }

        @media (min-width: 1200px) {
            font-size: 16px;
            line-height: 1.75;
        }
    }

    &__description:nth-child(odd) {
        @media (min-width: 1200px) {
            max-width: 288px;
        }

    }
}

.item1::before {
    content: url(../images/group.png);
}

.item2::before {
    content: url(../images/apples.png);
}

.item3::before {
    content: url(../images/heart.png);
}